.storyCard {
    width: 95%;
    height: 100%;
  }
  
  .story {
    margin: 1rem 0;
    /* width: 20rem; */
    height: 25rem;
  
    border-radius: 1rem;
  }
  .storyImg {
    width: 100%;
    height: 100%;
    opacity: 0.4;
    border-radius: 1rem;
  }
  
  .storyDes {
    color: white;
    position: relative;
    top: 74%;
    padding: 1rem;
  }
  .storyTitle {
    font-size: 1.5rem;
  }
  .storyContent {
    margin-top: 0.2rem;
    letter-spacing: 0.1rem;
  }
  .centerStory {
    width: 20rem;
    height: 30rem;
    position: relative;
    left: 35%;
    background: linear-gradient(#00000099, #00000099);
  }
  .editStory {
    display: inline-flex;
    align-items: center;
    background-color: white;
    border-radius: 1rem;
    padding: 0.5rem;
    box-shadow: 0px 4px 4px 0px #00000040;
    position: relative;
    left: 37%;
    top: -8%;
  }