
.skeleton_loader {
    background-color:#a58080;
      margin: 1rem 0;
     width: 20rem;
     height: 25rem;
     border-radius: 1rem;
     animation: skeleton-animation 0.5s ease-in-out infinite;
   
     }
     
     /* skeleton effect */
     @keyframes skeleton-animation {
       0% {
         opacity: 0.7; 
       }
       50% {
         opacity: 0.5; 
       }
       100% {
         opacity: 0.7;
       }
     }
     