.story_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 50%;
    padding: 2rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.26);
    background-color: white;
    z-index: 2;
  }
  .story_form__small {
    width: 90% !important;
    padding: 1.5rem !important;
  }
  
  .storySlide__container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  
    /* overflow-x: scroll; */
  
    text-align: center;
  }
  .slide_box {
    padding: 0.6rem;
    border-radius: 10%;
    text-align: center;
    margin-right: 1rem;
    display: inline-flex;
    justify-content: center;
    box-shadow: 0px 4px 15px 0px #0000001a;
  }
  
  /* Slide Form */
  .slideForm,
  .slideForm__container {
    width: 100%;
  }
  .story_close {
    position: relative;
    left: 52%;
    top: -4.8rem;
  }
  .slideForm__content {
    width: 100%;
  }
  .slideForm__input {
    width: 75%;
    border: 1px solid black;
    outline: none;
    padding: 0.5rem;
    margin-bottom: 1rem;
  
    font-size: 1rem;
  }
  
  .slideForm__input:focus {
    border: 1.5px solid #7eff73;
  }
  .slideForm__input::placeholder {
    color: #847c7c;
  }
  
  .slideForm__label {
    width: 27%;
    font-size: 1.2rem;
  }
  
  .input_container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .slideForm__button {
    color: red;
    border: none;
    background-color: white;
    font-size: 1.2rem;
    cursor: pointer;
    border-radius: 50%;
    padding: 0.5rem;
    margin: 0.5rem;
    position: relative;
  }
  
  .buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .form_err {
    padding: 1rem;
    color: red;
  }
  .slideForm__remove {
    position: relative;
    top: -0.4rem;
    left: 0.5rem;
  }
  
  @media screen and (max-width: 768px) {
    .story_form {
      width: 90%;
      flex-direction: row;
    }
    .input_container {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .slideForm__label {
      width: 100%;
      font-size: 0.8rem;
    }
    .slideForm__input {
      width: 100%;
      font-size: 0.6rem;
    }
    .storySlide__container {
      width: 40%;
      flex-direction: column;
      padding: 4rem 0;
    }
    .slide_box {
      padding: 0.5rem;
      border-radius: 3rem;
      text-align: center;
      font-size: 0.8rem;
      margin-right: 0.5rem;
      margin-bottom: 0.8rem;
    }
    .buttons {
      position: absolute;
      bottom: 2%;
    }
    .form_err {
      position: absolute;
      bottom: 2rem;
    }
    .story_close {
      position: absolute;
      top: 0.8rem;
      left: 87%;
    }
    .buttons {
      width: 95%;
    }
  }